import React from 'react';
import {Grid, Typography, createMuiTheme, ThemeProvider, Box, Card, CardMedia, CardContent} from '@material-ui/core';
import { Reveal } from 'semantic-ui-react';
import 'semantic-ui-css/semantic.min.css';

const theme= createMuiTheme({
	overrides: {

		MuiCardMedia :{
			media: {
				backgroundColor: "black",
				height: "auto",
				maxHeight: "80vh",
				width: "auto",
				maxWidth: "60vh",
			}
		},
		MuiCardContent: {
			root: {
				height: "auto",
				maxHeight: "80vh",
				width: "auto",
				maxWidth: "60vh",
				backgroundColor: "black",
				color: "white"

			}
		},
		MuiGrid: {
			"grid-xs-6": {
				flexBasis: 'auto',
			}
		},
		MuiTypography:{
	    	h4: {
	    		fontFamily: "Futura"
	    	},
	    	h5: {
	    		fontSize: "1.25rem",
	    		fontFamily: "Futura"
	    	},
	    	body1: {
	    		fontSize: "1.25rem",
	    		fontFamily: "Futura"
	    	},
	    	body2: {
	    		fontSize: "1.25rem",
	    		fontFamily: "Futura",
	    		fontWeight: "bold"
	    	},
		}
	}
})


class ExperienceItem extends React.Component {
	constructor(props){
		super(props);
	}

	render(){
		return (
		<ThemeProvider theme={theme}>
			<Box paddingTop="2rem" paddingBottom="2rem">
				<Grid container alignItems="center" justify="center">
					<Box paddingRight="5rem">
						<Grid item>
							<Card>
								<CardMedia component="img" image={this.props.logo}/>
								<CardContent>
									<Typography variant="h4">{this.props.company}</Typography>
									<Typography variant="h5">{this.props.title}</Typography>
								</CardContent>
							</Card>
						</Grid>
					</Box>
					<Box paddingLeft="5rem">
						<Grid item>
							<Card>
								<CardContent>
									<Typography variant="body1">{this.props.date}</Typography>
									{this.props.description.map((item) =>
										(<Box paddingTop="1rem" paddingBottom="1rem">
												<Typography variant="body1">{item}</Typography>
										</Box>)
									)}
									<Box paddingTop="1rem" paddingBottom="1rem">
									<Typography variant="body2">{this.props.skills}</Typography>
									</Box>
								</CardContent>
							</Card>
						</Grid>
					</Box>

				</Grid>
			</Box>
		</ThemeProvider>
		)
	}

}

export default ExperienceItem;